import { _Main_Template } from "./_Main_Template";
import { data } from "../data";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";

export function Main_Node() {
    useEffect(() => {}, []);

    const elements = [];
    data.forEach((_data, idx) => {
        elements.push(
            <Link
                to={`/node/${idx}`}
                key={idx}
                style={{
                    textDecorationLine: "none",
                    display: "flex",
                    flexDirection: "column",
                    flex: "1 1",
                    minWidth: "200px",
                    maxWidth: "240px",
                    Width: "225px",
                    aspectRatio: "1080/1920",
                    overflow: "hidden",
                    margin: "10px",
                    color: "black",
                }}
            >
                <img
                    style={{ borderRadius: "20px", width: "100%", height: "80%", objectFit: "fill" }}
                    src={_data.path + "/thumbnail.jpg"}
                ></img>
                <div style={{ fontWeight: "500", padding: "4px" }}>{_data.title}</div>
            </Link>
        );
    });
    const body = <div style={{ flex: "1 1", display: "flex", flexDirection: "row", flexWrap: "wrap" }}>{elements}</div>;

    return <_Main_Template left={Left()} right={Right()} body={body}></_Main_Template>;
}

const Left = () => {
    return <div></div>;
};
const Right = () => {
    return <div></div>;
};

const Body = () => {
    return <div>aaa</div>;
};
