import { _Main_Template } from "./_Main_Template";
import { data } from "../data";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import "../App.css";

export function Main_Node_Post() {
    const { post } = useParams();
    const [is_set, set_is_set] = useState(false);
    const [jsondata, set_jsondata] = useState({});

    useEffect(() => {
        fetch("/" + data[parseInt(post)].path + "/data.json").then((response) => {
            response.json().then((_jsondata) => {
                set_jsondata(_jsondata);
                set_is_set(true);
            });
        });
        // data[parseInt(post)].path
    }, []);

    if (is_set) {
        let basepath = "/" + data[parseInt(post)].path + "/";
        return <_Main_Template left={Left()} right={Right()} body={Body(basepath, jsondata)}></_Main_Template>;
    } else {
        return <_Main_Template left={Left()} right={Right()} body={<div>loading...</div>}></_Main_Template>;
    }
}

const Left = () => {
    return <div></div>;
};
const Right = () => {
    return <div></div>;
};

const Body = (basepath, jsondata) => {
    let savednodepaths = jsondata.savednodepaths;
    let stepdata = jsondata.stepdata;

    return (
        <div
            style={{
                flex: "1 1",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
            }}
        >
            <button
                style={{ backgroundColor: "rgb(125,0,200)" }}
                onClick={() => {
                    if (window.Python) {
                        savednodepaths.forEach((_data, idx) => {
                            let nodename = _data.nodename;
                            let parentpath = _data.parentpath;
                            window.Python.runStatements(`
import hou
node = hou.node("${parentpath}")
node.loadItemsFromFile("http://localhost:3000${basepath + nodename}")
#node.loadItemsFromFile("https://graph1111.github.io/houdini${basepath + nodename}")

`);
                        });
                    }
                }}
            >
                 
            </button>
            {stepdata.map((data, idx) => {
                return (
                    <div
                        key={idx}
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "center",
                            alignItems: "center",
                            padding: "30px 0px",
                        }}
                    >
                        <button
                            onClick={() => {
                                if (window.Python) {
                                    window.Python.runStatements(`
import hou
import hdefereval

sceneViewer = hou.ui.paneTabOfType(hou.paneTabType.SceneViewer)
networkEditor = hou.ui.paneTabOfType(hou.paneTabType.NetworkEditor)
viewport = sceneViewer.curViewport()    


node = hou.node("${data.selectednodepath}")
networkEditor.cd(node.parent().path())
networkEditor.setCurrentNode(node)
networkEditor.requestZoomReset()
networkEditor.homeToSelection()
if(node.type().category().name() !="Lop"): 
    node.setRenderFlag(True)
node.setDisplayFlag(True)

if(node.type().category().name() =="Lop"):
    sceneViewer.setHydraRenderer("Karma XPU")    

viewport.homeAll()

def a2():
    viewport.frameSelected()                                        
hdefereval.executeDeferred(a2 )

`);
                                }
                            }}
                        >
                              
                        </button>
                        <div>
                            {data.imgdata.map((imgpath, idx2) => {
                                return <img key={idx2} style={{ borderRadius: "15px" }} src={basepath + imgpath}></img>;
                            })}
                        </div>
                        <div>{<div>{data.textdata}</div>}</div>
                    </div>
                );
            })}
            <button
                style={{ backgroundColor: "rgb(125,0,200)" }}
                onClick={(e) => {
                    if (window.Python) {
                        savednodepaths.forEach((_data, idx) => {
                            let nodename = _data.nodename;
                            let parentpath = _data.parentpath;
                            window.Python.runStatements(`
import hou
node = hou.node("${parentpath + "/" + nodename}")
if(node.path() =="/stage"):
    node.deleteItems(node.allItems())
else:
    node.parent().deleteItems((node,))

`);
                        });
                    }
                }}
            >
                Clear All Nodes
            </button>
        </div>
    );
};
