function _Main_Left({ left }) {
    return <div>{left}</div>;
}
function _Main_Right({ right }) {
    return <div>{right}</div>;
}
function _Main_Body({ body }) {
    return <div style={{ display: "flex", flex: "1 1", padding: "20px" }}>{body}</div>;
}

export function _Main_Template({ left, right, body }) {
    return (
        <div style={{ flex: "1 1" }}>
            <div style={{ display: "flex", flex: "1 1" }}>
                <_Main_Left left={left}></_Main_Left>
                <_Main_Body body={body}></_Main_Body>
                <_Main_Right right={right}></_Main_Right>
            </div>
        </div>
    );
}
