import { useEffect } from "react";

import { Header } from "./components/Header";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { Home } from "./pages/Home";
import { Node } from "./pages/Node";
import { Node_Post } from "./pages/Node_Post";
import { Tutorial } from "./pages/Tutorial";
import { About } from "./pages/About";

import chan from "./qwebchannel.js";

//initialize
window.onload = () => {
    if (!window.qt || !window.qt.webChannelTransport) {
        return;
    }
    new chan.QWebChannel(window.qt.webChannelTransport, function (channel) {
        window.Python = channel.objects.Python;
    });
};

export default () => {
    return (
        <div>
            <script language="javascript" src="/qwebchannel.js"></script>
            <BrowserRouter>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/node" element={<Node />} />
                    <Route path="/node/:post" element={<Node_Post />} />
                    <Route path="/tutorial" element={<Tutorial />} />
                    <Route path="/about" element={<About />} />
                </Routes>
            </BrowserRouter>
        </div>
    );
};
