import { Link } from "react-router-dom";
export function Header() {
    let style = { padding: "8px", textDecorationLine: "none", color: "black", fontSize: "1rem" };

    return (
        <div>
            <div
                style={{
                    flex: "1 1",
                    display: "flex",
                    backgroundColor: "rgba(10,10,10,0.1)",
                    fontWeight: "500",
                    // cursor: "pointer",
                }}
            >
                <div style={{ padding: "10px" }}></div>
                <div style={{ flex: "1 1", display: "flex", justifyContent: "center" }}>
                    <Link style={style} to="/">
                        Home
                    </Link>
                    <Link style={style} to="/node">
                        Node
                    </Link>
                    <Link style={style} to="/tutorial">
                        Tutorial
                    </Link>
                    <Link style={style} to="/about">
                        About
                    </Link>
                </div>
                <div style={{ padding: "10px" }}></div>
            </div>
        </div>
    );
}
